.search {
	display: flex;
	align-items: center;
	justify-content: space-between;
	border: 1px solid #e0e3ee;
	border-radius: 10px;
	padding: 5px;
	height: 100%;
	width: 274px;
	user-select: none;
}

.search input {
	border: none;
	outline: none;
	color: #2d3b87;
	font-size: 15px;
	margin-left: 20px;
}

.search button {
	background: #2d3b87;
	border-radius: 5px;
	padding: 10px;
	cursor: pointer;
	border: none;
	display: flex;
	justify-content: center;
	align-items: center;
}
